@import url(//fonts.googleapis.com/css?family=Playfair+Display);
@import url(//fonts.googleapis.com/css?family=Merriweather);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(241, 238, 241);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --nav-bar-height: 7vh;
  --fontsize-nav: calc(5px + 1.5vw);
  --default-header-font: 'Playfair Display';
  --default-text-font: 'Merriweather';
  --default-title-font: 'Merriweather';
  --default-highlight-color: rgba(29, 233, 206, 0.966);
  --default-header-color: rgba(22, 182, 161, 0.966);
  --default-title-color: rgba(11, 155, 136, 0.966);
  --default-margin: 14px;
  --default-margin-top: 1.9vh;
  --default-margin-left: 2vw;
  --navigation-percent: 70%;
  --h1-content-font-size: 3.5vw;
  --text-content-font-size: 1.8vw;
}

.App {
  display: grid;
  grid-template-rows: 0px 0px 0px;
}

@media only screen and (max-width: 1500px) {
  :root{
    --fontsize-nav: calc(2px + 1.7vw);
    --default-margin: 8px;
    --navigation-percent: 68%;
    
  }
}

@media only screen and (max-width: 1000px) {
  :root{
    --fontsize-nav: calc(2px + 2vw);
    --default-margin: 8px;
    --default-margin-top: 2.5vh;
    --navigation-percent: 55%;
    --h1-content-font-size: 5vw;
    --text-content-font-size: 1.9vw;
  }
}

@media only screen and (max-width: 600px) {
  :root{
    --fontsize-nav: 3.5vw;
    --default-margin: 2.2vh;
    --default-margin-top: 1.7vh;
    --default-margin-left: 4vw;
    --navigation-percent: 34%;
    --h1-content-font-size: 6vw;
    --text-content-font-size: 4vw;
  }
}
.NavigationBar {
    background-color: rgba(0,0,0,0.6);
    height: var(--nav-bar-height);
    min-height: 50px;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center; 
}

.navigationItems {
  width: 95%;
  display: grid;
  grid-template-columns: var(--navigation-percent) 1.1fr 0.9fr 0.9fr;
}

.active {
    font-weight: bolder;
}

@media only screen and (max-width: 600px) {
  .NavigationBar {
    min-height: 50px;
  }
}

@media only screen and (max-width: 600px) {
    .NavigationBar {
        background-color: rgba(0,0,0,0.7);
        grid-template-columns: var(--navigation-percent) 0.9fr 0.75fr 1fr;
        min-height: 40px;
    }
}
.NavigationItem {
  margin-left: 1vw;
  font-family: var(--default-header-font);
  font-style: normal; 
  font-feature-settings: normal; 
  font-variant: normal; 
  font-size: var(--fontsize-nav);
  text-decoration: none;
  color: white;
}

.NavigationActive {
  font-weight: bolder;
  color: var(--default-highlight-color);
}

li {
  list-style-type: none !important;
}

@media only screen and (max-width: 600px) {
  .NavigationItem {
    margin-left: 1.5vw;
  }
}


.Header {
  background-image: url(/portfolio-project/static/media/header-desktop-2.d52e4f64.jpg);
  background-size: cover;
  background-position: center;
  z-index: 0;
  width: 100%;
  height: 60vh;
  min-height: 700px;
  font-size: calc(10px + 1vw);
  color: white;
  box-shadow: 0 5px 10px -2px gray;
}

.HeaderName{
  font-family: var(--default-header-font);
  position: relative;
  width: 50%;
  top: 40%;
  left: 30vw;
  color: white;
}

.HeaderRole{
  font-family: var(--default-header-font);
  position: relative;
  width: 50%;
  top: 40%;
  left: 30vw;
  color: var(--default-highlight-color);
}

.HeaderButton{
  background-color: var(--default-highlight-color);
  width: 11% !important;
  height: 4vh !important;
  border-width: 1px;
  border-radius: 4vh;
  border-color: var(--default-highlight-color);
  cursor: pointer;
  position: relative;
  display: block;
  top: 45%;
  left: 30vw;
  font-family: var(--default-text-font);
  font-size: calc(7px + 1vw);
}

.HeaderButton:focus{
  outline: none;
}

@media only screen and (max-width: 1500px) {
  .Header {
    background-image: url(/portfolio-project/static/media/header-desktop-2.d52e4f64.jpg);
    min-height: 350px;
  }

  .HeaderButton{
    width: 14% !important;
    height: 30px !important;
    font-size: calc(7px + 0.8vw);
  }
}

@media only screen and (max-width: 1000px) {
  .Header {
    background-image: url(/portfolio-project/static/media/header-desktop-3.2fc0d2ff.jpg);
    min-height: 300px;
    font-size: calc(10px + 2vw);
  }

  .HeaderButton{
    width: 17.5% !important;
    height: 30px !important;
  }
}

@media only screen and (max-width: 600px)  {
  .Header {
    background-image: url(/portfolio-project/static/media/header-mobile.7d6d91f2.jpg);
    min-height: 300px;
    text-align: center;
    align-items: center;
    justify-content: center;
  }

  .HeaderName{
    position: relative;
    width: 50%;
    top: 40%;
    left: 0vw;
    display: block;
    margin-left: auto;
    margin-right: auto;
  }

  .HeaderRole{
    position: relative;
    width: 50%;
    top: 40%;
    left: 0vw;
    display: block;
    margin-left: auto;
    margin-right: auto;
  }

  .HeaderButton{
    position: relative;
    width: 33vw !important;
    height: 10% !important;
    top: 43%;
    left: 0vw;
    font-size: 3.5vw;
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
}
.HomeContent {
  margin-top: 8vh;
  margin-left: auto;
  margin-right: auto;
  width: 85%;
  height: 100%;
  display: grid;
  grid-template-rows: 85px 65px;
  text-align: center;
}

.HomeContent h1 {
  font-family: var(--default-title-font); 
  font-style: normal; 
  font-feature-settings: normal; 
  font-variant: normal; 
  font-weight: 1000; 
  line-height: 26.4px;
  font-size: var(--h1-content-font-size);
  color: var(--default-title-color);
}

.HomeContent p {
  font-family: var(--default-text-font); 
  font-style: normal; 
  font-feature-settings: normal; 
  font-variant: normal; 
  font-weight: 500;
  font-size: var(--text-content-font-size);
}

.imageSpan {
  transform: scale(100%, 1.1fr);
  width: 100%;
  height: 100%;
  margin-top: -50px;
  grid-row: 1;
  display: none;
}

@media only screen and (max-width: 1500px) {
  .HomeContent {
    margin-top: 5vh;
    grid-template-columns: none;
    grid-template-rows: 70px 65px;
  }
}

@media only screen and (max-width: 600px) {
  .HomeContent {
    margin-top: 5vh;
    grid-template-columns: none;
    grid-template-rows: 75px 65px;
  }
}

.Experience {
  margin-top: calc(40px + 5vmin);
  margin-left: auto;
  margin-right: auto;
  width: 85%;
  height: 100%;
  text-align: center;
}

.Experience h1 {
  font-family: var(--default-title-font); 
  font-style: normal; 
  font-feature-settings: normal; 
  font-variant: normal; 
  font-weight: 1000; 
  line-height: 26.4px;
  font-size: var(--h1-content-font-size);
  color: var(--default-title-color);
}

.experienceCards {
  align-items: center;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}
.ExperienceCard {
  display: block;
  margin-top: auto;
  margin-left: auto;
  margin-right: auto;
  height: 60vh;
  width: 20vw;
  border: 2px solid rgba(0,0,0,0.7);
  border-radius: 3%;
}

.ExperienceCard h1 {
  display: block;
  margin-top: 2vh;
  margin-left: auto;
  margin-right: auto;
}
